@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.App{display:-webkit-flex;display:flex}

.menu-list__container{background-color:#f9f9f9;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;height:100vh;min-width:25%;border-right:1px solid #e0e0e0;overflow:auto;position:relative}.menu-list__container>.menu{padding:15px}.menu-list__container a{text-decoration:none;color:black;margin-left:0.5em;display:-webkit-flex;display:flex;-webkit-align-items:flex-start;align-items:flex-start;font-size:12px;margin-bottom:0.2em;position:relative}.menu-list__container a:visited{color:inherit}.menu-list__container a.nested-link:hover{text-decoration:underline}.menu-list__container a.selected-link{font-weight:bold}.disclose-arrow{width:7px;height:7px;background:url(/web-help-menu/static/media/down-arrow.493ab8dc.svg) no-repeat center;transition-duration:0.5s;padding-top:1em;position:absolute;left:-1em}.disclose-arrow.rotated{-webkit-transform:rotate(-180deg);transform:rotate(-180deg)}.disclose-arrow.hidden{display:none}.submenu{padding-left:15px}.link-background{width:100%;position:absolute;background-color:rgba(0,0,0,0.1);left:0;display:none}.link-background.active{display:block}.link-background.highlighted{background-color:yellow;display:block}

.content__container{padding:30px;width:65%;height:100%}.content__container h1{font-weight:bold;font-size:40px}.content__container h2{font-size:25px}.content__container a{text-decoration:none;color:inherit}.content__container a:visited{color:inherit}.content__container .content{margin-left:2em}

html{color:#000;background:#fff}body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td{margin:0;padding:0}table{border-collapse:collapse;border-spacing:0}fieldset,img{border:0}address,caption,cite,code,dfn,em,strong,th,var{font-style:normal;font-weight:normal}ol,ul{list-style:none}caption,th{text-align:left}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal}q:before,q:after{content:''}abbr,acronym{border:0;-webkit-font-feature-settings:normal;font-feature-settings:normal;font-variant:normal}sup{vertical-align:text-top}sub{vertical-align:text-bottom}input,textarea,select{font-family:inherit;font-size:inherit;font-weight:inherit}input,textarea,select{*font-size:100%}legend{color:#000}#yui3-css-stamp.cssreset{display:none}

body{margin:0;font-family:'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

